@charset "UTF-8";
/* 共通設定 */
html body {
  height: 100%;
}

#wrap {
  display: grid;
  width: 100vw;
  height: 100vh;
  min-height: 100%;
  grid-template-columns: 15% 1fr;
  grid-template-rows: auto;
  grid-template-areas: "container_left container_right";
  background-color: #ffcdd2;
  overflow: auto;
}

/* グリッド */
.container_left {
  grid-area: container_left;
  background-color: #7fcac3;
  position: relative;
}

.container_right {
  grid-area: container_right;
  display: grid;
  grid-template-rows: 15% 60% 25%;
  grid-template-areas: "top_index_area" "login_area" "submit_area";
}

/* 共通バー */
/* トップロゴ */
.top_logo {
  position: relative;
}
.top_logo p {
  position: absolute;
  color: #7fcac3;
  font-weight: bold;
  font-size: 2.5rem;
  top: 50%;
  left: 60%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}
.top_logo img {
  display: block;
  width: 80%;
  margin: 2em auto;
}

/* リンク */
.link {
  width: 80%;
  margin: 1em auto;
  padding-top: 2em;
}

.link_back {
  position: absolute;
  bottom: 1rem;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}

a.btn-border {
  display: block;
  padding: 1rem;
  border: 0.2em dashed #ffcdd2;
  border-radius: 0.5rem 0.5rem 0.5rem 0.5rem;
  text-decoration: none;
  text-align: center;
  color: #ffcdd2;
  font-weight: bold;
  font-size: 2em;
}

/*  ログインフォーム  */
.top_index {
  grid-area: top_index_area;
  font-size: 2em;
  padding: 2em;
}

.login {
  grid-area: login_area;
  width: 100%;
  height: auto;
}

.form_group {
  width: 100%;
  height: auto;
  font-size: 2em;
  padding: 1em 1em 1em 3em;
}

.registration_form label {
  display: inline-block;
  width: 5em;
}

.login_submit {
  grid-area: submit_area;
  width: 100%;
  text-align: center;
  padding: 2em;
}
.login_submit input {
  display: block;
  border: 1px solid transparent;
  border-radius: 10%;
  padding: 0.5em;
  margin: auto;
  width: 30%;
  max-height: 7em;
  font-size: 3em;
  background: #7fcac3;
  color: #ffffff;
}

